.everyCard__wrapper {
  position: relative;
}
.everyCard__wrapper .everyCard__parentBlock {
  width: 320px;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 15px;
  -webkit-box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.7);
          box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.7);
}
@media screen and (max-width: 1120px) {
  .everyCard__wrapper .everyCard__parentBlock {
    width: 420px;
  }
}
@media screen and (max-width: 1120px) and (max-width: 990px) {
  .everyCard__wrapper .everyCard__parentBlock {
    width: 380px;
  }
}
@media screen and (max-width: 1120px) and (max-width: 990px) and (max-width: 910px) {
  .everyCard__wrapper .everyCard__parentBlock {
    width: 420px;
  }
}
@media screen and (max-width: 1120px) and (max-width: 990px) and (max-width: 910px) and (max-width: 590px) {
  .everyCard__wrapper .everyCard__parentBlock {
    width: 320px;
  }
}
.everyCard__wrapper .everyCard__parentBlock .everyCard__childBlock {
  position: relative;
  border-radius: 15px 15px 0 0;
}
.everyCard__wrapper .everyCard__parentBlock .everyCard__childBlock h3 {
  font-family: "Unbounded", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  color: #000000;
  position: absolute;
  padding: 5px 10px;
  background-color: #fff;
  border-radius: 15px 0px;
}
.everyCard__wrapper .everyCard__parentBlock .everyCard__childBlock p {
  padding: 5px 10px;
  position: absolute;
  right: 0;
  bottom: 4px;
  font-family: "Unbounded", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 8px;
  line-height: 10px;
  color: #000000;
  border-radius: 15px 0px 0px 0px;
  background-color: #fff;
}
.everyCard__wrapper .everyCard__parentBlock .everyCard__childBlock .everyCard__infoImg > img {
  width: 100%;
  height: 240px;
  border-radius: 15px 15px 0 0;
}
.everyCard__wrapper .everyCard__parentBlock .block_description {
  padding: 11px 10px;
  background: transparent;
  border-radius: 0 0 15px 15px;
}
.everyCard__wrapper .everyCard__parentBlock .block_description h4 {
  font-family: "Unbounded", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 17px;
  color: #1e1e1e;
  height: 18px;
  overflow: hidden;
}
.everyCard__wrapper .everyCard__parentBlock .block_description .description_text {
  font-weight: 300;
  font-size: 10px;
  line-height: 14px;
  color: #000;
  margin: 11px 0px 15px;
  height: 56px;
  overflow: hidden;
}
.everyCard__wrapper .everyCard__parentBlock .block_description > button {
  width: 30px;
  height: 30px;
  background: transparent;
  border: none;
}